.alg-dtwp-label:hover .alg-dtwp-tip{
	opacity:1;
	top:-30px;
	visibility: visible;
}
.alg-dtwp-tip{
	font-weight:normal;
	font-size:13px;
	visibility: hidden;
	opacity:0;
	white-space: nowrap;
	right:0;
	top:-20px;
	position:absolute;
	padding:6px 10px;
	transition:all 0.25s ease-in-out;
}